<template>
      <div>
        <el-col :span="14" :offset="5">
          <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>{{this.topic}}</span>
      </div>
      <div class="text item">
        <ul>
          <li>
            {{this.content1}}
          </li>
        </ul>
      </div>
          </el-card>
        </el-col>
      </div>
</template>

<script>
    export default {
       name: "Single",
       data(){
          return {
            content1:'',
            topic:''
          }
       },
      mounted(){
        console.log('开始订阅')
         this.content1 = this.$route.params.content
         this.topic = this.$route.params.topic
        console.log(this.$route.params.topic)
        this.$pub.subscribe('data',(msg,content)=>{
          console.log('bug通过了')
          this.content1 = content
        })
        console.log('订阅结束')
      },
     /* mounted(){此处只是测试一下而已，我的猜测以及常规套路是正确的
         console.log('6446464')
        this.$pub.subscribe('d',(msg,content)=>{
          console.log('bug通过了')
          console.log(content)
          this.content1 = content
        })
      }*/
    }
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
    height: 500px;
    background-color: #434343;
    color: #C7C7C7;
    border: none;
  }
</style>



